import "./interests.scss";

import foosballIcon from "../../assets/icons/table-football_thin.png";
import cameraIcon from "../../assets/icons/camera_thin.png";
import paintIcon from "../../assets/icons/farbe.png";
import brainIcon from "../../assets/icons/gehirn.png";
import controllerIcon from "../../assets/icons/joystick.png";
import musicIcon from "../../assets/icons/musical-note_thin.png";
import travelIcon from "../../assets/icons/travel-luggage.png";

export default function Interests() {
    return (
        <div className="interests">
            <div className="interestsContainer">
                <h1 className="interestsTitle">
                    Interessen
                </h1>
                <div className="iconsContainer">
                    <div className="iconContainer" id="iconOne">
                        <img className="foosballIcon" src={foosballIcon} alt="foosball icon"/>
                    </div>
                    <div className="iconContainer" id="iconTwo">
                        <img className="cameraIcon" src={cameraIcon} alt="camera icon"/>
                    </div>
                    <div className="iconContainer" id="iconThree">
                        <img className="paintIcon" src={paintIcon} alt="paint icon"/>
                    </div>
                    <div className="iconContainer" id="iconFour">
                        <img className="brainIcon" src={brainIcon} alt="foosball icon"/>
                    </div>
                    <div className="iconContainer" id="iconFive">
                        <img className="controllerIcon" src={controllerIcon} alt="controller icon"/>
                    </div>
                    <div className="iconContainer" id="iconSix">
                        <img className="musicIcon" src={musicIcon} alt="music icon"/>
                    </div>
                    <div className="iconContainer" id="iconSeven">
                        <img className="travelIcon" src={travelIcon} alt="travel icon"/>
                    </div>
                </div>
            </div>
        </div>
    );
}